
.imgs-header{
  float: left;
  width: 50%;
  height: 35px;
  line-height: 35px;
  text-align: center;
  font-size:0.9em;
  border: 1px solid #ccc;
  box-sizing: border-box;
}
.img-div{
  height: 60px;
}
.img{
  width: 31%;
  float: left;
  height: 60px;
  margin: 3px 0.5% 0 0.5%;
  text-align: center;
  line-height: 60px;
  border: 2px solid #ccc;
  color: #ddd;
}



