
.cell_coverphoto img{
    width: 40px;
    height: 40px;
    margin-right: 15px;
}
.letter{
    margin-left:10px;
}
.list-tab{
  position: fixed;
  right: 0;
  top:10%;
  color: #000;
}
.list-tab ul li{
    list-style:none;
}
.cars{
  position:fixed;
  right:0;
  top:0;
  height: 100%;
  width: 70%;
  background:#fff;
  display: none;
}
.cars img{
    width: 90px;
    height: 60px;
    margin-right: 5px;
}
.mask{
  position:fixed;
  left: 0;
  top: 0;
  height:100%;
  width: 30%;
  background: #bbb;
  opacity: 0.3;
  display: none;
}



