
.price-top{
  color: #fff;
  background: rgb(89, 175, 128);
  line-height: 1.8em;
  font-size: 1em;
  height: 1.8em;
  text-align: center;
}
.header-img{
  width: 35%;
  border-radius: 5px;
  border:1px solid #bbb;
  margin-left:3%;
  margin-top:15px;
}
.header-content{
  float: right;
  width: 60%;
  box-sizing: border-box;
}
.header-alias-name{
  font-weight: bold;
  font-size: 1.2em;
}
.pcard-header{
  background: #ddd;
  color: #666;
  font-size: 0.9em;
  padding: 3px 8px;
}
.pcard-footer{
  background: #ddd;
  color: #666;
  font-size: 0.9em;
  padding: 3px 8px;
  margin-top:8px;
}
.price-footer{
  width: 100%;
  height: 43px;
  font-size: 1.1em;
  line-height:43px;
  text-align: center;
  color: #fff;
  background: rgb(105, 176, 247);
}



