
.car-img{
  height: auto;
  width:100%;
}
.car-header{
  height: 80px;
}
.car-header-left{
  box-sizing:border-box;
  width: 50%;
  float: left;
  padding-top:5px;
  padding-left:15px;
}
.car-header-right{
  width: 50%;
  float: left;
}
.car-header-right button{
  background:rgb(93, 176, 243);
  border-radius:5px;
  color: #fff;
  width:200px;
  line-height:50px;
  font-size:1.3em;
  margin-top: 10px;
}
.car-body ul li{
  display: inline;
  list-style: none;
}
.car-card-header{
  background:#eee;
  color:#999;
  padding: 5px;
}
.car-card-content{
  padding-bottom:20px;
  padding-left:5px;
}
.car-card-footer{
  color: rgb(43, 181, 245);
  text-align: center;
  line-height:40px;
  border-top:1px solid #ddd;
}
.car-footer{
  position: fixed;
  bottom: 0;
  width: 100%;
  text-align: center;
  color: #fff;
  background: rgb(105, 176, 247);
}



